@charset "UTF-8";

@import 'partials/_mixin';
@import 'partials/_variables';

:root {
	--vh: 100%;
}

html, 
body {
	position: relative;
	width: 100%;
}

body {
	@include overflow-scroll(default, dark, large, y);

	&.hidden {
		overflow: hidden;
	}
}

#wrap {
	overflow: clip;
	display: flex;
	flex-direction: column;
	min-height: calc(var(--vh, 1vh) * 100);
}

.skip {
	position: fixed;
	top: -50px;
	left: 0;
	z-index: 10000;
	width: 100%;
	height: 50px;
	background-color: var(--color-base-000);

	a {
		display: flex;
		justify-content: center;
		align-items: center;
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		background-color: var(--color-base-000);
		color: var(--color-base-100);
		text-decoration: underline;
		text-align: center;

		&:not(:focus) {
			top: -50px
		}
	}

	&:focus-within {
		top: 0;
	}
}

#header {
	--gnb-color: var(--color-base-000);
	--lnb-color: var(--color-base-000);
	--stroke-color: var(--color-base-000);

	flex: 1 1 100%;
	display: flex;
	position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
    width: 100%;
	background-color: rgb(255, 255, 255);
	transition: background-color 0.2s ease-in-out;

	&.header-invert {
		--gnb-color: var(--color-base-100);
		--lnb-color: var(--color-base-100);
		--stroke-color: var(--color-base-100);

		background-color: transparent;
		
		&::before {
			border-bottom: 0;
		}

		
		
		&:not(.active, :focus-within, :focus, :hover) {
			// --gnb-color: var(--color-base-100);
			// --lnb-color: var(--color-base-100);

			// background-color: transparent;
	
			// &::before {
			// 	border-bottom: 0;
			// }

			// .header-inner {
			// 	.logo {
			// 		svg {
			// 			path {
			// 				fill: var(--color-base-100);
			// 			}
			// 		}
			// 	}
			// }
		}

		@include respond-below(tablet) {
			--gnb-color: var(--color-base-000);
			--lnb-color: var(--color-base-000);
			--stroke-color: var(--color-base-100);

			&.open {
				--stroke-color: var(--color-base-000);
				background-color: var(--color-base-100);
			}
		}

		@include root-selector('.active-scroll-start') {
			--gnb-color: var(--color-base-000);
			--lnb-color: var(--color-base-000);
			--stroke-color: var(--color-base-000);

			border-bottom: 1px solid #C6C6C6;
			background-color: var(--color-base-100);
		}
	}
	
	&::before {
		content: "";
        display: block;
        position: absolute;
        z-index: -1;
        top: 0;
        bottom: 0;
        left: calc(50% - 50vw);
        width: 100vw;
		height: 100%;
		box-sizing: border-box;
		border-bottom: 1px solid #C6C6C6;
    }

	.header-inner {
		display: flex;
		justify-content: space-between;
		column-gap: 12px;
		width: 100%;
		// max-width: 1816px;
		margin: 0 auto;
		padding: 0 52px;
		box-sizing: border-box;

		@include respond-below(tablet) {
			flex: 1 1 100%;
			column-gap: 0;
			max-width: 100%;
			margin: 0;
			padding: 0 24px;
		}

		.logo {
			display: block;
			width: 150px;
			height: 84px;
			line-height: 84px;

			.logo-link {
				display: inline-flex;
				align-items: center;
				height: 100%;
			}

			svg {
				width: 100%;
			}
		}

		.gnb {
			display: flex;
			justify-content: center;
			position: relative;
			height: 100%;

			.gnb-list {
				display: flex;

				&.focused,
				&.active {
					.gnb-item {
						height: 200px;
					}

					.lnb-list {
                        opacity: 1;
						visibility: visible;
                    }
				}

				.gnb-item {
					height: 84px;
					transition: height 0.15s ease-in-out;;

					.gnb-text {
						display: inline-flex;
						align-items: center;
						justify-content: center;
						width: 140px;
						height: 84px;
						padding: 0 15px;
						box-sizing: border-box;
						color: var(--gnb-color);
						text-align: center;
					}
				}

				.lnb-list {
					opacity: 0;
					visibility: hidden;
					transition: opacity 0.125s ease, visibility 0.125s ease;

					.lnb-item {
						.lnb-text {
							display: block;
							font-size: 14px;
							line-height: 24px;
							color: var(--lnb-color);
							text-align: center;

							&:hover {
								text-decoration: underline;
							}
						}
					}
				}
			}

			@include respond-below(tablet) {
				.gnb-list {
					flex-direction: column;
					// row-gap: 20px;
					position: fixed;
					top: 84px;
					left: -100%;
					width: 100%;
					height: calc(var(--vh, 1vh) * 100 - 84px);
					background-color: #fff;
					transition: left ease 0.5s 0.1s;

					&.focused,
					&.active {
						left: 0;

						.gnb-item {
							height: auto;
						}
					}
	
					.gnb-item {
						display: flex;
						height: auto;
						// transition: height 0.15s ease-in-out;

						+.gnb-item {
							border-top: 1px solid var(--color-base-080);
						}
	
						.gnb-text {
							flex-shrink: 0;
							display: flex;
							align-items: center;
							justify-content: center;
							min-width: auto;
							width: 110px;
							height: auto;
							padding: 20px 8px;
							background-color: var(--color-base-090);
							text-align: center;
						}
					}
	
					.lnb-list {
						flex: 1 1 100%;
						opacity: 0;
						visibility: hidden;
						transition: opacity 0.125s ease, visibility 0.125s ease;

						.lnb-item {
							.lnb-text {
								padding: 10px;
								text-align: left;
							}
						}
					}
				}
			}
		}

		.navi-util {
			@include respond-below(tablet) {
				margin-left: auto;
			}

			.navi-list {
				display: flex;
				column-gap: 8px;

				@include respond-below(tablet) {
					column-gap: 0;
				}

				.navi-item {
					position: relative;
					color: var(--gnb-color);

					&.mypage {
						&:hover,
						&:focus-within {
							.util-list {
								display: block;
							}
						}

						&:not(:hover, :focus-within) {
							.util-list {
								display: none;
							}
						}
					}

					.navi-text {
						display: inline-flex;
						align-items: center;
						justify-content: center;
						padding: 0 15px;
						height: 84px;
						text-align: center;

						@include respond-below(tablet) {
							padding: 0 8px;

							&::before {
								content: '';
								display: block;
								width: 20px;
								height: 20px;
								background-color: var(--color-base-080);
							}

							.em {
								@include hidden;
							}
						}
					}

					.util-list {
						position: absolute;
						top: 100%;
						left: 50%;
						min-width: 100%;
						margin-top: -10px;
						padding: 20px;
						border-radius: 8px;
						background-color: var(--color-base-090);
						transform: translateX(-50%);

						&::before {
							content: '';
							position: absolute;
							top: auto;
							bottom: 100%;
							left: 50%;
							width: 0;
							height: 0;
							border-top: 10px solid transparent;
							border-right: 10px solid transparent;
							border-bottom: 10px solid var(--color-base-090);
							border-left: 10px solid transparent;
							transform: translateX(-50%);
						}

						.util-item {
							display: block;
							font-size: 14px;
							line-height: 24px;
							text-align: center;

							&:hover {
								text-decoration: underline;
							}

							.util-text {
								color: var(--color-base-010);
							}
						}
					}
				}
			}
		}

		.navi-control {
			display: none;

			@include respond-below(tablet) {
				display: flex;
			}

			.btn-navi-menu {
				position: relative;

				&.active {
					svg {
						.ob-01 { transform: rotate(45deg); }
						.ob-02 { transform: scaleX(0.1); }
						.ob-03 { transform: rotate(-45deg); }
					}
				}

				svg {
					.ob-01 { transform: translateY(-20%) }
					.ob-03 { transform: translateY(20%); }
					
					.ob-01,
					.ob-02,
					.ob-03 {
						stroke: var(--stroke-color);
					}

					line {
						transform-origin: center;
						stroke:  var(--stroke-color);
						stroke-width: 5%;
						transition-duration: 0.25s;
						transition-property: transform;
					}
				}
			}
		}
	}
}

#footer {
	//flex: 1 1 100%;
	box-shadow: inset 0 1px 0 0 var(--color-base-070);
	background-color: var(--color-base-090);

	.footer-inner {
		padding: 40px 20px;
	}
}

.container {
	flex: 1 1 100%;
	display: flex;
	padding: 164px 80px 80px;
	box-sizing: border-box;

	&:has(.in-between) {
		background-color: var(--color-base-090);
	}

	@include respond-below(tablet) {
		padding: 120px 20px 80px;
	}

	.in-between {
		display: flex;
		align-items: flex-start;
		column-gap: 40px;

		.left-side {
			flex-shrink: 0;
			padding: 24px 24px 48px;
			box-sizing: border-box;
			background-color: var(--color-base-100);

		}

		.right-side {
			flex: 1 1 100%;
			padding: 48px 0;
			box-sizing: border-box;
			background-color: var(--color-base-100);
		}
	}
}