:root {
  --vh: 100%;
}

html,
body {
  position: relative;
  width: 100%;
}

@media (max-width: 1023px) {
  body {
    overflow-x: hidden;
    overflow-y: auto;
  }
}
@media (min-width: 1024px) {
  body {
    -webkit-overflow-scrolling: touch;
    overflow-x: hidden;
    overflow-y: overlay;
  }
  body::-webkit-scrollbar-thumb {
    border-width: 4px;
    border-style: solid;
    border-color: transparent;
    background-clip: content-box;
    background-color: rgba(0, 0, 0, 0.2);
  }
  body::-webkit-scrollbar-thumb:hover {
    background-color: rgba(0, 0, 0, 0.4);
  }
  body::-webkit-scrollbar-thumb:active {
    background-color: rgba(0, 0, 0, 0.6);
  }
  body::-webkit-scrollbar {
    width: 16px;
    height: 16px;
  }
  body::-webkit-scrollbar-thumb {
    border-radius: 8px;
  }
}
body.hidden {
  overflow: hidden;
}

#wrap {
  overflow: clip;
  display: flex;
  flex-direction: column;
  min-height: calc(var(--vh, 1vh) * 100);
}

.skip {
  position: fixed;
  top: -50px;
  left: 0;
  z-index: 10000;
  width: 100%;
  height: 50px;
  background-color: var(--color-base-000);
}
.skip a {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: var(--color-base-000);
  color: var(--color-base-100);
  text-decoration: underline;
  text-align: center;
}
.skip a:not(:focus) {
  top: -50px;
}
.skip:focus-within {
  top: 0;
}

#header {
  --gnb-color: var(--color-base-000);
  --lnb-color: var(--color-base-000);
  --stroke-color: var(--color-base-000);
  flex: 1 1 100%;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100%;
  background-color: rgb(255, 255, 255);
  transition: background-color 0.2s ease-in-out;
}
#header.header-invert {
  --gnb-color: var(--color-base-100);
  --lnb-color: var(--color-base-100);
  --stroke-color: var(--color-base-100);
  background-color: transparent;
}
#header.header-invert::before {
  border-bottom: 0;
}
@media (max-width: 1023px) {
  #header.header-invert {
    --gnb-color: var(--color-base-000);
    --lnb-color: var(--color-base-000);
    --stroke-color: var(--color-base-100);
  }
  #header.header-invert.open {
    --stroke-color: var(--color-base-000);
    background-color: var(--color-base-100);
  }
}
html.active-scroll-start #header.header-invert {
  --gnb-color: var(--color-base-000);
  --lnb-color: var(--color-base-000);
  --stroke-color: var(--color-base-000);
  border-bottom: 1px solid #C6C6C6;
  background-color: var(--color-base-100);
}

#header::before {
  content: "";
  display: block;
  position: absolute;
  z-index: -1;
  top: 0;
  bottom: 0;
  left: calc(50% - 50vw);
  width: 100vw;
  height: 100%;
  box-sizing: border-box;
  border-bottom: 1px solid #C6C6C6;
}
#header .header-inner {
  display: flex;
  justify-content: space-between;
  column-gap: 12px;
  width: 100%;
  margin: 0 auto;
  padding: 0 52px;
  box-sizing: border-box;
}
@media (max-width: 1023px) {
  #header .header-inner {
    flex: 1 1 100%;
    column-gap: 0;
    max-width: 100%;
    margin: 0;
    padding: 0 24px;
  }
}
#header .header-inner .logo {
  display: block;
  width: 150px;
  height: 84px;
  line-height: 84px;
}
#header .header-inner .logo .logo-link {
  display: inline-flex;
  align-items: center;
  height: 100%;
}
#header .header-inner .logo svg {
  width: 100%;
}
#header .header-inner .gnb {
  display: flex;
  justify-content: center;
  position: relative;
  height: 100%;
}
#header .header-inner .gnb .gnb-list {
  display: flex;
}
#header .header-inner .gnb .gnb-list.focused .gnb-item, #header .header-inner .gnb .gnb-list.active .gnb-item {
  height: 200px;
}
#header .header-inner .gnb .gnb-list.focused .lnb-list, #header .header-inner .gnb .gnb-list.active .lnb-list {
  opacity: 1;
  visibility: visible;
}
#header .header-inner .gnb .gnb-list .gnb-item {
  height: 84px;
  transition: height 0.15s ease-in-out;
}
#header .header-inner .gnb .gnb-list .gnb-item .gnb-text {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 140px;
  height: 84px;
  padding: 0 15px;
  box-sizing: border-box;
  color: var(--gnb-color);
  text-align: center;
}
#header .header-inner .gnb .gnb-list .lnb-list {
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.125s ease, visibility 0.125s ease;
}
#header .header-inner .gnb .gnb-list .lnb-list .lnb-item .lnb-text {
  display: block;
  font-size: 14px;
  line-height: 24px;
  color: var(--lnb-color);
  text-align: center;
}
#header .header-inner .gnb .gnb-list .lnb-list .lnb-item .lnb-text:hover {
  text-decoration: underline;
}
@media (max-width: 1023px) {
  #header .header-inner .gnb .gnb-list {
    flex-direction: column;
    position: fixed;
    top: 84px;
    left: -100%;
    width: 100%;
    height: calc(var(--vh, 1vh) * 100 - 84px);
    background-color: #fff;
    transition: left ease 0.5s 0.1s;
  }
  #header .header-inner .gnb .gnb-list.focused, #header .header-inner .gnb .gnb-list.active {
    left: 0;
  }
  #header .header-inner .gnb .gnb-list.focused .gnb-item, #header .header-inner .gnb .gnb-list.active .gnb-item {
    height: auto;
  }
  #header .header-inner .gnb .gnb-list .gnb-item {
    display: flex;
    height: auto;
  }
  #header .header-inner .gnb .gnb-list .gnb-item + .gnb-item {
    border-top: 1px solid var(--color-base-080);
  }
  #header .header-inner .gnb .gnb-list .gnb-item .gnb-text {
    flex-shrink: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: auto;
    width: 110px;
    height: auto;
    padding: 20px 8px;
    background-color: var(--color-base-090);
    text-align: center;
  }
  #header .header-inner .gnb .gnb-list .lnb-list {
    flex: 1 1 100%;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.125s ease, visibility 0.125s ease;
  }
  #header .header-inner .gnb .gnb-list .lnb-list .lnb-item .lnb-text {
    padding: 10px;
    text-align: left;
  }
}
@media (max-width: 1023px) {
  #header .header-inner .navi-util {
    margin-left: auto;
  }
}
#header .header-inner .navi-util .navi-list {
  display: flex;
  column-gap: 8px;
}
@media (max-width: 1023px) {
  #header .header-inner .navi-util .navi-list {
    column-gap: 0;
  }
}
#header .header-inner .navi-util .navi-list .navi-item {
  position: relative;
  color: var(--gnb-color);
}
#header .header-inner .navi-util .navi-list .navi-item.mypage:hover .util-list, #header .header-inner .navi-util .navi-list .navi-item.mypage:focus-within .util-list {
  display: block;
}
#header .header-inner .navi-util .navi-list .navi-item.mypage:not(:hover, :focus-within) .util-list {
  display: none;
}
#header .header-inner .navi-util .navi-list .navi-item .navi-text {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0 15px;
  height: 84px;
  text-align: center;
}
@media (max-width: 1023px) {
  #header .header-inner .navi-util .navi-list .navi-item .navi-text {
    padding: 0 8px;
  }
  #header .header-inner .navi-util .navi-list .navi-item .navi-text::before {
    content: "";
    display: block;
    width: 20px;
    height: 20px;
    background-color: var(--color-base-080);
  }
  #header .header-inner .navi-util .navi-list .navi-item .navi-text .em {
    overflow: hidden;
    position: absolute;
    top: -9999em;
    left: -9999em;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    border: 0;
    line-height: 1px;
    font-size: 1px;
    border: 0;
    clip: rect(0, 0, 0, 0);
    pointer-events: none;
    display: block;
    position: absolute;
  }
}
#header .header-inner .navi-util .navi-list .navi-item .util-list {
  position: absolute;
  top: 100%;
  left: 50%;
  min-width: 100%;
  margin-top: -10px;
  padding: 20px;
  border-radius: 8px;
  background-color: var(--color-base-090);
  transform: translateX(-50%);
}
#header .header-inner .navi-util .navi-list .navi-item .util-list::before {
  content: "";
  position: absolute;
  top: auto;
  bottom: 100%;
  left: 50%;
  width: 0;
  height: 0;
  border-top: 10px solid transparent;
  border-right: 10px solid transparent;
  border-bottom: 10px solid var(--color-base-090);
  border-left: 10px solid transparent;
  transform: translateX(-50%);
}
#header .header-inner .navi-util .navi-list .navi-item .util-list .util-item {
  display: block;
  font-size: 14px;
  line-height: 24px;
  text-align: center;
}
#header .header-inner .navi-util .navi-list .navi-item .util-list .util-item:hover {
  text-decoration: underline;
}
#header .header-inner .navi-util .navi-list .navi-item .util-list .util-item .util-text {
  color: var(--color-base-010);
}
#header .header-inner .navi-control {
  display: none;
}
@media (max-width: 1023px) {
  #header .header-inner .navi-control {
    display: flex;
  }
}
#header .header-inner .navi-control .btn-navi-menu {
  position: relative;
}
#header .header-inner .navi-control .btn-navi-menu.active svg .ob-01 {
  transform: rotate(45deg);
}
#header .header-inner .navi-control .btn-navi-menu.active svg .ob-02 {
  transform: scaleX(0.1);
}
#header .header-inner .navi-control .btn-navi-menu.active svg .ob-03 {
  transform: rotate(-45deg);
}
#header .header-inner .navi-control .btn-navi-menu svg .ob-01 {
  transform: translateY(-20%);
}
#header .header-inner .navi-control .btn-navi-menu svg .ob-03 {
  transform: translateY(20%);
}
#header .header-inner .navi-control .btn-navi-menu svg .ob-01,
#header .header-inner .navi-control .btn-navi-menu svg .ob-02,
#header .header-inner .navi-control .btn-navi-menu svg .ob-03 {
  stroke: var(--stroke-color);
}
#header .header-inner .navi-control .btn-navi-menu svg line {
  transform-origin: center;
  stroke: var(--stroke-color);
  stroke-width: 5%;
  transition-duration: 0.25s;
  transition-property: transform;
}

#footer {
  box-shadow: inset 0 1px 0 0 var(--color-base-070);
  background-color: var(--color-base-090);
}
#footer .footer-inner {
  padding: 40px 20px;
}

.container {
  flex: 1 1 100%;
  display: flex;
  padding: 164px 80px 80px;
  box-sizing: border-box;
}
.container:has(.in-between) {
  background-color: var(--color-base-090);
}
@media (max-width: 1023px) {
  .container {
    padding: 120px 20px 80px;
  }
}
.container .in-between {
  display: flex;
  align-items: flex-start;
  column-gap: 40px;
}
.container .in-between .left-side {
  flex-shrink: 0;
  padding: 24px 24px 48px;
  box-sizing: border-box;
  background-color: var(--color-base-100);
}
.container .in-between .right-side {
  flex: 1 1 100%;
  padding: 48px 0;
  box-sizing: border-box;
  background-color: var(--color-base-100);
}